import './App.css';
import React from 'react';
import RadTrix from './RadTrix';
import { Button, Col, Collapse, Input, InputNumber, Layout, PageHeader, Popover, Radio, Row, Select, Tag, Transfer, Typography } from 'antd';
import { CopyrightOutlined, InfoCircleOutlined } from '@ant-design/icons';
import { color, getIndex } from './helpers';
import { data } from './data2';
import { useRecoilState, useSetRecoilState } from 'recoil';
import { circleNodeSortedIdx, currentIndexAtom, dataAtom, edgeTypeAtom, orderTypeAtom } from './state';
import { cloneDeep, range } from 'lodash';

const { Header, Footer, Sider, Content } = Layout;
const { Panel } = Collapse;
const { Option } = Select;

// const headerStyle = {
